import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify'

import VueSweetalert2 from 'vue-sweetalert2';
import Vuelidate from 'vuelidate'
import VueHtmlToPaper from 'vue-html-to-paper';
import VueHtml2pdf from 'vue-html2pdf'
// import { Field, Form } from 'vee-validate';
let basePath = window.location.origin;
import "./sass/style.scss"
import "./sass/create_plan_style.scss";
// import "../public/styles/style.scss"
// import "../public/styles/create_plan_style.scss";
const options = {
  name: "EMED-ESWMS-F003 Rev. 1/10-15-2018",
  specs: ["fullscreen=yes", "titlebar=yes", "scrollbars=yes"],
  styles: [`${basePath}/styles/create_plan_style.scss`],
  windowTitle: "EMED-ESWMS-F003 Rev. 1/10-15-2018"
};
Vue.config.productionTip = false
Vue.use(VueHtmlToPaper, options);
//Vue.use(VueHtml2pdf)
// Vue.use(VueApexCharts)
Vue.use(Vuelidate)

new Vue({
  router,
  store,
  vuetify,
  VueSweetalert2,
  Vuelidate,
  //VueHtml2pdf,
  // Field,
  // Form,
  // VueApexCharts,
  render: h => h(App)
}).$mount('#app')
